import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import MenuButton from '@components/MenuButton';
import Sidebar from '@components/Sidebar';
import type { TitleState } from '@store/modules/title';
import type { RootState } from '@store/index';

import { HeaderLayout, HeaderSideBox, HeaderParagraph } from './Styles';

function Header() {
  const { title } = useSelector<RootState, TitleState>((state) => state.title);

  const [isMenuOpen, setMenuOpen] = useState(false);

  return (
    <HeaderLayout>
      <Sidebar isOpen={isMenuOpen} setIsOpen={setMenuOpen} />

      <HeaderSideBox>
        <MenuButton isOpen={isMenuOpen} setIsOpen={setMenuOpen} />
      </HeaderSideBox>

      <HeaderParagraph>{title}</HeaderParagraph>

      <HeaderSideBox />
    </HeaderLayout>
  );
}

export default Header;
