import React from 'react';
import { Link } from 'gatsby';
import { SidebarLayout } from './Styles';

interface Props {
  isOpen: boolean;
  setIsOpen: React.Dispatch<React.SetStateAction<boolean>>;
}

function Sidebar({ isOpen, setIsOpen }: Props) {
  const closeSidebarHandler = () => {
    setIsOpen(false);
  };

  return (
    <SidebarLayout isOpen={isOpen}>
      <div className="sidebar-dimmed" onClick={closeSidebarHandler} />
      <div className="sidebar-content">
        <p></p>
        <ul>
          <li>
            <Link to="/about" onClick={closeSidebarHandler}>
              About
            </Link>
          </li>
          <li>
            <Link to="/" onClick={closeSidebarHandler}>
              Home
            </Link>
          </li>
        </ul>
      </div>
    </SidebarLayout>
  );
}

export default Sidebar;
