const enum UPDATE_FILTER {
  CHANGE_EXT = 'CHANGE_EXT_FILTER',
  CHANGE_DATE = 'CHANGE_DATE_FILTER',
  CHANGE_KEYWORD = 'CHANGE_KEYWORD_FILTER',
}

export const updateExtFilter = (ext: string | null) => ({
  type: UPDATE_FILTER.CHANGE_EXT,
  ext,
});

export const updateDateFilter = (date: { start: Date; end: Date } | null) => ({
  type: UPDATE_FILTER.CHANGE_DATE,
  date,
});

export const updateKeywordFilter = (keyword: string) => ({
  type: UPDATE_FILTER.CHANGE_KEYWORD,
  keyword,
});

export type DateRange = {
  start: Date;
  end: Date;
};

export interface FiltersState {
  ext: string | null;
  date: DateRange | null;
  keyword: string;
}

interface FiltersAction {
  type: UPDATE_FILTER;
  ext?: string;
  date?: DateRange;
  keyword?: string;
}

const initialState = {
  ext: null,
  date: null,
  keyword: '',
};

const filters = (
  state: FiltersState = initialState,
  action: FiltersAction
): FiltersState => {
  switch (action.type) {
    case UPDATE_FILTER.CHANGE_KEYWORD: {
      if (typeof action.keyword !== 'string') {
        return state;
      }

      return { ...state, keyword: action.keyword };
    }

    case UPDATE_FILTER.CHANGE_DATE: {
      if (!action.date) {
        return { ...state, date: null };
      }

      const { start, end } = action.date;

      if (!(start instanceof Date) || !(end instanceof Date)) {
        return state;
      }

      return { ...state, date: action.date };
    }

    case UPDATE_FILTER.CHANGE_EXT: {
      if (!action.ext) {
        return { ...state, ext: null };
      }

      if (typeof action.ext !== 'string') {
        return state;
      }

      return { ...state, ext: action.ext };
    }
    default: {
      return state;
    }
  }
};

export default filters;
