import styled from 'styled-components';
import { size, color, animation } from '@styles';

interface MenuButtonLayoutProps {
  isOpen: boolean;
}

export const MenuButtonLayout = styled.div<MenuButtonLayoutProps>`
  position: relative;
  border: none;
  background: none;
  display: flex;
  flex-direction: column;
  width: 1.5rem;
  height: 1.2rem;
  z-index: ${size.MENU_BTN_Z_INDEX};
  cursor: pointer;
  > div {
    position: absolute;
    left: 0;
    width: 100%;
    height: 20%;
    border-radius: 0.1875rem;
    transition: background-color ease 0.5s, transform ease 0.2s;
    background-color: ${({ isOpen }) => (isOpen ? color.PRIMARY : 'white')};
    &:nth-child(1) {
      transform: rotate(${({ isOpen }) => (isOpen ? 45 : 0)}deg);
      top: ${({ isOpen }) => (isOpen ? 40 : 0)}%;
      ${animation.fadeIn(0.2)}
    }
    &:nth-child(2) {
      top: 40%;
      ${({ isOpen }) =>
        isOpen ? animation.fadeOut(0.4) : animation.fadeIn(0.2)}
    }
    &:nth-child(3) {
      transform: rotate(${({ isOpen }) => (isOpen ? -45 : 0)}deg);
      top: ${({ isOpen }) => (isOpen ? 40 : 80)}%;
      ${animation.fadeIn(0.2)}
    }
  }
`;
