import styled, { css } from 'styled-components';
import { animation, size, mixin, color } from '@styles';

interface SidebarLayoutProps {
  isOpen: boolean;
}

export const SidebarLayout = styled.div<SidebarLayoutProps>`
  position: fixed;
  inset: 0;
  visibility: ${({ isOpen }) => (isOpen ? 'visible' : 'hidden')};
  transition: visibility 0.7s;

  .sidebar-dimmed {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    position: absolute;

    ${({ isOpen }) => (isOpen ? animation.fadeIn() : animation.fadeOut())}
  }

  .sidebar-content {
    width: ${size.SIDEBAR_WIDTH};
    height: 100%;
    position: absolute;
    left: 0;
    background: white;
    padding: 9rem 3rem;
    overflow-x: hidden;
    box-shadow: 4px 0px 4px rgba(0, 0, 0, 0.05); // () shadow overflow     ?

    ${({ isOpen }) =>
      isOpen
        ? animation.slideInLeft(0.5, 0.1)
        : animation.slideOutLeft(0.5, 0.2)}

    ${mixin.mobile(css`
      width: 100%;
    `)}

    p {
      color: ${color.PRIMARY};
      font-size: 0.875rem;
      font-weight: bolder;
    }

    ul {
      padding: 0;

      li {
        list-style: none;
        font-size: 1.5rem;
        margin: 1rem 0;
        position: relative;
        cursor: pointer;

        a {
          color: black;
          text-decoration: none;
        }

        &:before {
          content: '';
          position: absolute;
          left: 0;
          right: 65%;
          top: 0;
          bottom: 0;
          background-color: ${color.SECONDARY};
          z-index: -1;
          border-radius: 3px;
          transform: translateX(1000px);
          transition: transform 0.4s;
        }

        &:hover {
          &:before {
            transform: translateX(0);
          }
        }
      }
    }
  }
`;
