import { css } from 'styled-components';

//  &
export const fadeOut = (duration = 1, delay = 0) => css`
  @-webkit-keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  -webkit-animation: fadeOut ${duration}s ease-out ${delay}s both;
  animation: fadeOut ${duration}s ease-out ${delay}s both;
`;

export const fadeIn = (duration = 1, delay = 0) => css`
  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  -webkit-animation: fadeIn ${duration}s ease-out ${delay}s both;
  animation: fadeIn ${duration}s ease-out ${delay}s both;
`;

//  &
export const slideInLeft = (duration = 1, delay = 0) => css`
  -webkit-animation: slide-in-left ${duration}s
    cubic-bezier(0.25, 0.46, 0.45, 0.94) ${delay}s both;
  animation: slide-in-left ${duration}s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    ${delay}s both;

  @-webkit-keyframes slide-in-left {
    0% {
      -webkit-transform: translateX(-1000px);
      transform: translateX(-1000px);
    }
    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
    }
  }
  @keyframes slide-in-left {
    0% {
      -webkit-transform: translateX(-1000px);
      transform: translateX(-1000px);
    }
    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
    }
  }
`;

export const slideOutLeft = (duration = 1, delay = 0) => css`
  -webkit-animation: slide-out-left ${duration}s
    cubic-bezier(0.55, 0.085, 0.68, 0.53) ${delay}s both;
  animation: slide-out-left ${duration}s cubic-bezier(0.55, 0.085, 0.68, 0.53)
    ${delay}s both;

  @-webkit-keyframes slide-out-left {
    0% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
    }
    100% {
      -webkit-transform: translateX(-1000px);
      transform: translateX(-1000px);
    }
  }
  @keyframes slide-out-left {
    0% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
    }
    100% {
      -webkit-transform: translateX(-1000px);
      transform: translateX(-1000px);
    }
  }
`;
