const enum UPDATE_TITLE {
  CHANGE_TITLE = 'CHANGE_TITLE',
}

export const changeTitle = (title: string) => ({
  type: UPDATE_TITLE.CHANGE_TITLE,
  title,
});

export interface TitleState {
  title: string | null;
}

interface TitleAction {
  type: UPDATE_TITLE;
  title: string;
}

const initialState = {
  title: null,
};

const title = (
  state: TitleState = initialState,
  action: TitleAction
): TitleState => {
  switch (action.type) {
    case UPDATE_TITLE.CHANGE_TITLE: {
      return { ...state, title: action.title };
    }
    default: {
      return state;
    }
  }
};

export default title;
